.card {
  border-radius: 10;
  background-color: #f9f9f9;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  margin: 10px;
  min-height: 170px;
  padding: 15px;
}

.my-col {
  padding: 10;
}

h1 {
  font-size: 1rem;
}
h5 {
  font-size: 0.8rem;
}

@media (min-width: 400px) {
  h1 {
    font-size: 1.6rem;
  }
  h2 {
    font-size: 1.2rem;
  }
  h4 {
    font-size: 1.2em;
  }
  /* h5 {
    font-size: 1.1rem;
  } */
}

@media (min-width: 600px) {
  h1 {
    font-size: 1.8rem;
  }
  h2 {
    font-size: 1.4rem;
  }
  h4 {
    font-size: 1.2em;
  }
  /* h5 {
    font-size: 1.1rem;
  } */
}

@media (min-width: 800px) {
  h1 {
    font-size: 2.1rem;
  }
  h2 {
    font-size: 1.6rem;
  }
  h4 {
    font-size: 1.2em;
  }
  /* h5 {
    font-size: 1.1em;
  } */
}

@media (min-width: 1000px) {
  h1 {
    font-size: 2.75rem;
  }
  h2 {
    font-size: 1.8rem;
  }
  h4 {
    font-size: 1.2rem;
  }
  /* h5 {
    font-size: 1.1rem;
  } */
}
